import React from 'react';
import { NavLink } from 'react-router-dom';




const Common = (props) =>

{

    return(

        <>
        <section id="header" className="d-flex align-item-center">
       <div className="container-fluid">
         <div className="row">
             <div className="col-10 mx-auto">


             <div className="row">             
                 
                 <div className="col-md-6 pt-5 pt-lg-0 order-2 order-lg-1 d-flex flex-column justify-content-center middle-content"> 
                  <h1>{props.headname} <strong className="brand-name">    AuraWeb Tech</strong> </h1>
                 <h2 className="my-3 sub-title">We are the team of talented developer making website</h2>
               <div className="mt-3">
               <NavLink to={props.visit} className="btn-get-started"> {props.btnname} </NavLink>
               </div>
               </div>
           
             <div className="col-lg-6 order-1 order-lg-2 header-img pt-5">
              <img src={props.imgsrc} className="img-fluid animated" alt="home_img" />
             </div>
             
             



             </div>


 
                           </div>
         </div>

         </div>

          
     </section>
    

        </>






    )

};
export default Common;