import React from 'react';
import { NavLink } from 'react-router-dom';
import logo from '../src/images/logo.jpeg';



const  Navbar=()=>

{

    return(

      <>
  <div className="conatiner-fluid nav-bg">
  <div className="row">
  <div className="col-10 mx-auto">

  <nav className="navbar navbar-expand-lg navbar-light bg-light">
   <NavLink  className="navbar-brand" to="/"><img src={logo} className="logo_img"/>  Aura WebTech</NavLink>
   <button className="navbar-toggler" type="button" data-toggle="collapse" data-target="#navbarSupportedContent" aria-controls="navbarSupportedContent" aria-expanded="false" aria-label="Toggle navigation">
    <span className="navbar-toggler-icon"></span>
    </button>

    <div className="collapse navbar-collapse" id="navbarSupportedContent">
    <ul className="navbar-nav ml-auto">
      <li className="nav-item">
        <NavLink exact activeClassName="menu-active" className="nav-link" to="/">Home</NavLink>
      </li>
      <li className="nav-item">
        <NavLink  exact activeClassName="menu-active" className="nav-link" to="/service">Service</NavLink>
      </li>
          <li className="nav-item">
        <NavLink exact activeClassName="menu-active" className="nav-link" to="/about">About Us</NavLink>
      </li>
      <li className="nav-item">
        <NavLink exact activeClassName="menu-active" className="nav-link" to="contact">Contact Us</NavLink>
      </li>
      
      
      
    </ul>
    
  </div>
</nav>


</div>

</div>
</div>


</>
    )
}

export default Navbar;